form {
  width: 80%;
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(185, 185, 185);
  padding: 20px;
}
label {
  padding: 5px;
  font-weight: 800;
}
button {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: auto;
  width: 30%;
}
input {
  width: 90%;
}
.container {
  width: 400px;
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 40;
  color: #010101;
  margin-left: 80px;
}
.title {
  margin: 0;
  padding: 0;
  padding: 20px;
}
